.container {
    max-width: 1325px;
    margin: auto;

    @media #{$smlg-device} {
        padding: 0 20px;
    }
}

.container-2 {
    max-width: 1600px;
    margin: auto;

    @media #{$smlg-device} {
        padding: 0 20px;
    }
}

.title-main-center {
    .title {
        text-align: center;
        font-size: 55px;
        color: #0E1C40;
        line-height: 1.3;

        @media #{$smlg-device} {
            font-size: 44px;
        }

        @media #{$md-layout} {
            font-size: 36px;

            br {
                display: none;
            }
        }

        @media #{$sm-layout} {
            font-size: 28px;

            br {
                display: none;
            }
        }
    }
}